//
// Utilities
// --------------------------------------------------

.radius-none {
  border-radius: 0;
}
.overflow-hidden {
  overflow: hidden;
}
.overflow-auto {
  overflow: auto;
}
.block {
  display: block;
}
.inline-block {
  display: inline-block;
}
.relative {
  position: relative;
}
.fixed {
  position: fixed;
}
.height-auto {
  min-height: 0;
}
.z-3 {
  z-index: 3;
}
.z-2 {
  z-index: 2;
}
.z-1 {
  z-index: 1;
}
.focus-weight:focus {
  outline: 0;
  font-weight: 700;
}
